<template>
	<view class="box">
		<!-- 此处要做banner -->
		<image lazy-load class="top-img" src="../../static/images/crowd_funding_banner.png" mode="aspectFill"></image>
		<view class="good-classified">
			<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
				<view class="goods">
					<view class="goods-box">
						<template v-for="(item,k) in data_list">
							<navigator class="goods-li" :url="'/pages/goods/crowd_funding_detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId" :key="k">
								<image lazy-load class="img scale45" :src="item.mainPicUrl" mode="aspectFill"></image>
								<view class="title-box">
									<view class="tips-box" v-if="data_list[k].spuLabel">
										<template v-for="(val,i) in data_list[k].spuLabel">
											<view class="width106 " :class="{'bg-btn':i==0,'border-btn':i==1}" :key="i">
												{{val}}
											</view>
										</template>
									</view>
									<view class="title">
										{{item.spuTitle}}
									</view>
									<view class="price-box">
										<view class="num font-bold">￥{{item.promptPrice | formatNum}} </view>
									</view>
									<template v-if="item.o2oMarketingCrowdFunding">
										<progressBar2 :size='"small"' :percent='item.o2oMarketingCrowdFunding.percent'></progressBar2>
										<view class="people-box">
											<view class="people-box-l">
												<view class="num">
													{{item.o2oMarketingCrowdFunding.participantsNumber}}
												</view>
												<view class="">
													参与人数
												</view>
											</view>
											<view class="people-box-l">
												<view class="num">
													{{item.o2oMarketingCrowdFunding.finishTotalAmount}}
												</view>
												<view class="">
													已筹
												</view>
											</view>
										</view>
									</template>
								</view>
							</navigator>
						</template>
					</view>
				</view>
			</mescroll-body>
		</view>
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	import areaBanner from '@/components/area-banner.vue'
	import progressBar2 from '@/components/progress-bar2.vue'
	import Vue from 'vue'
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				data_list: [],
				upOption: {
					page: {
						size: 6
					},
					textNoMore: "没有更多数据",
				},
			}
		},
		components: {
			areaBanner,
			progressBar2,
			MescrollBody
		},
		methods: {
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				this.$api.get('/o2oSpu/findHeadPage', {
					params: {
						pageno: pageNum,
						pagesize: pageSize,
						promptType: 'CROWD_FUNDING', //促销类型
						purchaseType: 'ZC', //购买类型
						onSale: 'SALE'
					}
				}).then((res) => {
					if (res.code && res.code != 200) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;
					for (let val of curPageData) {
						if (val.spuLabel) {
							val.spuLabel = val.spuLabel.split(',')
						}
						if(val.o2oMarketingCrowdFunding){
							let per=parseFloat(val.o2oMarketingCrowdFunding.finishTotalAmount/val.o2oMarketingCrowdFunding.conditionTotalAmount)
							let percent=per*100+'%'
							Vue.set(val.o2oMarketingCrowdFunding,'percent',percent)
						}
					}
					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			}
		}
		
	}
</script>

<style lang="scss">
	.top-img {
		width: 100%;
		height: 310rpx;
	}

	.people-box {
		width: 100%;
		display: flex;
		font-size: 18rpx;
		color: #999;

		.people-box-l {
			width: 50%;
			flex-shrink: 0;

			.num {
				color: #333;
				font-size: 26rpx;
				margin: 4rpx 0;
			}
		}
	}

	.good-classified {
		width: calc(100% - 68rpx);
		height: auto;
		margin: 20rpx auto;

	}

	.width106 {
		height: auto;
		font-size: 20rpx;
		margin-right:10rpx;
		padding: 6rpx 14rpx;
	}

	.goods-box {
		width: 100%;
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items: flex-start;

		.goods-li {
			flex-shrink: 0;
			width: 332rpx;
			background: rgba(255, 255, 255, 1);
			border-radius: 8rpx;
			overflow: hidden;
			margin-bottom: 34rpx;
			height: 540rpx;
			background: white;
			overflow: hidden;

			.img {
				width: 100%;
				height: 300rpx;
				background: white;
				display: flex;
			}
		}
	}

	.title-box {
		width: calc(100% - 40rpx);
		padding: 18rpx 20rpx;
		padding-top: 0;
		float: left;
		overflow: hidden;

		.tips-box {
			display: flex;
			margin: 5rpx 0;
		}

		.bg-btn {
			background: linear-gradient(90deg, rgba(250, 143, 134, 1) 0%, rgba(252, 191, 175, 1) 100%);
		}

		.title {
			font-size: 30rpx;
			margin: 5rpx 0;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;

		}

		.price-box {
			width: 100%;
			display: flex;
			align-items: center;

			.num {
				font-size: 32rpx;
				color: $color-primary;
			}

			.people-num {
				font-size: 20rpx;
				margin-left: 40rpx;
			}
		}
	}
</style>
